* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


body {
  background: rgb(2, 0, 36);
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(31, 7, 65, 1) 35%,
    rgba(8, 4, 31, 1) 100%,
    rgba(124, 8, 255, 1) 100%
  );
  color: #fff;
  font-family: "DM Sans", sans-serif;
}

nav {
  width: 100%;
  height: 12 0px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 30px 190px;
}

.navright {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 50px;
}

.navleft img {
  width: 100%;
  height: 47px;
  object-fit: contain;
}

.navright button {
  background: linear-gradient(90.35deg, #2563eb 0.36%, #d946ef 201.02%);
  width: 132px;
  height: 42px;
  border: none;
  border-radius: 5px;
  color: #fff;
  font-size: 14px;
}

.navright p {
  font-weight: 500;
  font-size: 14px;
}

header {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 30px;
}

.h-left p {
  color: #fff;
  font-size: 20px;
  font-weight: 500;
}

.h-left h1 {
  background: -webkit-linear-gradient(90.35deg, #2563eb 0.36%, #d946ef 201.02%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 58px;
  font-weight: 700;

}

.h-left button{
  width: 135px;
  height: 61px;
  border: none;
  border-radius: 6px;
   background: linear-gradient(90.35deg, #2563EB 0.36%, #D946EF 201.02%);
}


.s-top{
  text-align: center;
}
.s-top h1{
font-size: 42px;
}
.s-top p{
font-size: 16px;
padding-top: 20px;
line-height: 26px;
font-weight: 400;
}

.cardla{
  width: 370px;
  height: 336px;
  border: 1px solid;
  border-image-source: linear-gradient(97.12deg, rgba(255, 255, 255, 0.08) -3.52%, rgba(255, 255, 255, 0.07) 100.35%);
  border-radius: 34px;
  text-align: center;
  padding-top: 30px;
  box-shadow: 0px 22px 18px 0px #0000000D;
  background: linear-gradient(97.12deg, rgba(255, 255, 255, 0.08) -3.52%, rgba(255, 255, 255, 0.07) 100.35%),
linear-gradient(268.12deg, rgba(254, 254, 255, 0.032) -11.04%, rgba(255, 255, 255, 0.018) 104.89%);

}

.ka{
  padding: 30px;
}

.s-card{
  padding:20px 350px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}



.section2{
display: flex;
align-items: center;
justify-content: center;
gap: 30px;

}

.sect2-left img{
  width: 100%;
  height: max-content;
  object-fit: contain;
}

.sect2-right h1{
  font-size: 42px;
}
.sect2-right p{
  padding-top: 50px;
  padding-bottom: 50px;
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
}

.sect2-right button{
  background: linear-gradient(90.35deg, #2563eb 0.36%, #d946ef 201.02%);
  width: 132px;
  height: 42px;
  border: none;
  border-radius: 5px;
  color: #fff;
  font-size: 14px;
}

.s0-card{
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
}

.card1{
  width: 270px;
  height: 266px;
  background: linear-gradient(97.12deg, rgba(255, 255, 255, 0.08) -3.52%, rgba(255, 255, 255, 0.07) 100.35%),
linear-gradient(268.12deg, rgba(254, 254, 255, 0.032) -11.04%, rgba(255, 255, 255, 0.018) 104.89%);
border: 1px solid;
border-image-source: linear-gradient(97.12deg, rgba(255, 255, 255, 0.08) -3.52%, rgba(255, 255, 255, 0.07) 100.35%);
box-shadow: 0px 22px 18px 0px #0000000D;
text-align: center;
border-radius: 34px;
margin-top: 60px;
padding-top: 15px;
}

.card1 p{
  padding: 10px;
}

.a1{
display: flex;
align-items: center;
justify-content: center;
padding: 100px;
}



.footer{
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

.footer img{
  width: 100%;
  height: max-content;
  object-fit: contain;
}